import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
// import ReactDOM from "react-dom";
import '@ionic/react/css/core.css';

// import * as dbActions from './components/DB/Db';
import Menu from './components/Menu/Menu';
import EntryRender from './components/EntryRender/EntryRender';
import Setup from './components/Setup/Setup';
import GDrive from './Server/GDrive'
// const writeYamlFile = require('write-yaml-file');
 

class App extends Component {

  // componentDidMount(){
  //   writeYamlFile('app.yaml').then(() => {
  //     console.log('done')
  //   });
  // }

  render () {
    return(    
        <>
          <Switch>
            <Route path='/new-entry' component={EntryRender} />
            <Route path='/setup' >
              <GDrive>
                <Setup/>
              </GDrive>
            </Route> 
            <Route path='/' exact >
              <GDrive>
                <Menu />
              </GDrive>
            </Route>
            <Route path='/authenticate.php' >
              <script src="./authenticate.php"/>
            </Route> 
          </Switch>
        </>    
    );
  }
}

export default App;



