import React, {Component} from 'react';

import * as dbActions from '../DB/Db';
import NewEntry from '../NewEntry/NewEntry';
import ErrorPage from '../NewEntry/ErrorPage/ErrorPage';



class EntryRender extends Component{

    state = { render: null}

    //determines whether to render data entry page or not based on whether there's a pending upload
    componentDidMount(){
        var that = this;

        dbActions.getDoc(dbActions.finalSub)
            .then(function(doc){
                // if submission pending server upload
                if(doc){
                    that.setState({render: <ErrorPage history={that.props.history}/>});
                }
                else{
                    that.setState({render: <NewEntry history={that.props.history} />});  
                }
            });

    }

    render(){
        return this.state.render;
    }


}

export default EntryRender;