import React from 'react';
import LongPress from 'react-long';

//returns a row that can be clicked by a hold down
function Holdable({id, onClick, onHold, children}) {

    const [timer, setTimer] = React.useState(null)
    
    function onPointerDown(evt) {
      const event = { ...evt } // convert synthetic event to real object
      const timeoutId = window.setTimeout(timesup.bind(null, event), 10000)
      setTimer(timeoutId)
    }
    
    function onPointerUp(evt) {
      if (timer) {
        window.clearTimeout(timer)
        setTimer(null)
        onClick(evt)
      }
    }
    
    function timesup(evt) {
      setTimer(null)
      onHold(evt)
    }
    
    if( window.navigator.platform === "Win32" ){
      return(
        <tr
        style={{overflow: "inherit"}}
          onPointerDown={onPointerDown}
          onPointerUp={onPointerUp}
          id={id}>
            {children}
        </tr>
      )
      
    }else{
      return (
        <LongPress
          time={1000}
          onLongPress={onHold}
          onPress={onClick}>
           <tr
            style={{overflow: "inherit"}}
            id={id}>
              {children}
          </tr>
        </LongPress>
     
        )
    }

    
  }

  export default Holdable;