import React from 'react';
// import {nav, Navbar, strong, header} from 'react-bootstrap';

import * as navClass from './Nav.css';
// import Logo from '../../Logo/Logo';
// import * as logoClass from '../../Logo/Logo.module.css';
import img from '../../../assets/images/Logo on blue background.JPG'

//functional class to output header bar w/ logo

const Nav = ( ) => (
    <header className="header sticky-top">
        <nav className="navbar navbar-expand-lg navbar-light bg-white py-3 shadow-sm">
            <div className="container" style={{height: "10%"}}>
                <img style={{height:"10%", width: "auto"}} alt="britton price" src={img} />
                <strong className={navClass.h1} style={{fontSize:"xxx-large"}}>Engineer Stock Control</strong>   
                <h3 style={{float: "right"}}>ver 1.0</h3>          
            </div>
        </nav>
    </header>
);

export default Nav;



