export const Table = (props) => {
  
  const columns = props.columns;
  const data = props.data;
  //const editRow = props.editRow;

  //get row index
  const getIndex = (rowProp) => {
    const rowIndex = rowProp.key.split("_");
    return rowIndex[1];
  }

  //on row interaction, trigger edit row 
  function rowClickListener (index, editRow)  {
    const rowIndex = index.key.split("_");
    editRow(rowIndex[1]);
  }

  // Use the state and functions returned from useTable to build your UI
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page, // has only the rows for the active page
    // canPreviousPage,
    // canNextPage,
    // pageOptions,
    // pageCount,
    // gotoPage,
    // nextPage,
    // previousPage,
    // setPageSize,
    //state: { pageIndex, pageSize },
  } = useTable(
    {
      columns,
      data,
      // initialState: { pageIndex: 0 , pageSize: 4}
    },
    usePagination
  )

  

  // Render the UI for your table
  return (
    <>
      <table style={{height: "80%"}} {...getTableProps()}>
        <thead>
          {headerGroups.map(headerGroup => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map(column => (
                <th {...column.getHeaderProps()}>{column.render('Header')}</th>
              ))}
            </tr>
          ))}
        </thead>
        <Auxiliary>
          <tbody {...getTableBodyProps()}>
            {page.map((row, i) => {
              prepareRow(row)
              return (
                //replaced tr tags with a custom to enable hold down 
                <Holdable {...row.getRowProps()}
                  onClick={() => console.log(row.getRowProps())}
                  onHold={() => rowClickListener(row.getRowProps(), props.editRow)}
                  id={getIndex(row.getRowProps())}>
                    {row.cells.map(cell => {
                      return <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
                    })}
                </Holdable>
                  
              )
            })}
          </tbody>
        </Auxiliary>
        
      </table>
      {/* 
        Pagination
      */}
      {/* <div className="pagination" style={{fontSize:"150%"}}>
        <button style={{width: "10%", height: "auto"}} onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
          {'<<'}
        </button>{' '}
        <button style={{width: "10%", height: "auto"}} onClick={() => previousPage()} disabled={!canPreviousPage}>
          {'<'}
        </button>{' '}
        <button style={{width: "10%", height: "auto"}} onClick={() => nextPage()} disabled={!canNextPage}>
          {'>'}
        </button>{' '}
        <button style={{width: "10%", height: "auto"}} onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
          {'>>'}
        </button>{' '}
        <span style={{color: 'black'}}>
          Page{' '}
          <strong style={{color: 'black'}}>
            {pageIndex + 1} of {pageOptions.length}
          </strong>{' '}
        </span>
      </div> */}
    </>
  )
}

