import React,{Component} from 'react';
import { withRouter } from 'react-router-dom';
import Popup from 'reactjs-popup';

import menuClass from '../Menu/Menu.module.css';
// import buttonClass from '../UI/Button/Button.css'
import Spacer from '../UI/Spacer/Spacer'
// import formClass from '../UI/Form/Form.css';
import Nav from '../UI/Nav/Nav';
import * as dbActions from '../DB/Db'
// import footerClass from '../UI/Footer/Footer.css'
import Footer from '../UI/Footer/Footer';
import Auxiliary from '../../containers/hoc/Auxiliary/Auxiliary';
import '../UI/Tabs/Tabs.css';

class Setup extends Component {



    state = {
        engNo: null,
        engName: null,
        engEmail: null,
        submitColor: "green",
        popupMessage: ' ',
        messagePopup: false
    }

    //on mount: resets data if needed, pulls setup information from database and populates accordingly
    componentDidMount() {
        var that = this;
        dbActions.getDoc(dbActions.setup)
            .then(function(doc){
                if(!doc){
                    dbActions.createOrUpdateDoc({_id: dbActions.setup,engNo: "",engName: "",engEmail: ""});
                }
                else{
                    that.setState({popupMessage: ' ', engNo: doc.engNo, engName: doc.engName, engEmail: doc.engEmail});
                }
                
            })
            .catch(function(err){
                console.log(err);
            });

           
    }

    //typing input handler, updates state accordingly
    inputHandler = (event, inId) => {
        switch(inId){
            case "id":
                this.setState({engNo: event.target.value});
                break;
            case "name":
                this.setState({engName: event.target.value});
                break;
            case "email":
                this.setState({engEmail: event.target.value});
                break;
            default: break;
        }
    }

     
    //submits data to d/b on button click
    submitHandler = () => {
        const that = this;

        try{
            if(this.state.engNo === null || this.state.engName ==="" || this.state.engEmail === ""){
                that.setState({submitColor: "#a84232", popupMessage: "Submit Failed - form incomplete", messagePopup: true});
            }
            // else if(!(this.state.engEmail.value.match(/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/))){
            //     that.setState({submitColor: "#a84232", popupMessage: "Submit Failed - invalid email", messagePopup: true});
            // }
            else{
                let docIn = {
                    _id: dbActions.setup,
                    // data
                    engNo: this.state.engNo,
                    engName: this.state.engName,
                    engEmail: this.state.engEmail
                }
    
                dbActions.createOrUpdateDoc(docIn, function(result){
                    console.log(result);
                    if (result)
                    {
                        that.setState({ submitColor: "#15a12f", popupMessage: "Submit Successful", messagePopup: true});
                    }else{
                        that.setState({submitColor: "#a84232", popupMessage: "Submit Failed", messagePopup: true});
                    }
                });
            }
           

            
        } catch (error){
            this.setState({submitColor: "#a84232", popupMessage: "Submit Failed"});
            console.log(error);
        }
        
    }

    uploadHandler = () => {
        
        const that = this;
        this.props.manualRun(function(result){
            console.log(result);
            that.setState({
                popupMessage: result === true? "Stock Uploaded": result || "Stock Upload failed - no stock to upload, check internet or contact admin", 
                submitColor: result === true? "#15a12f":"#a84232", 
                messagePopup: true});
        });
    }

    cleanupHandler = () => {
        
        const that = this;
        dbActions.cleanup(function(result){
            that.setState({
                popupMessage: result.ok? "Database cleanup complete": "Database cleanup failed", 
                submitColor: result.ok?"#15a12f":"#a84232", 
                messagePopup: true});
        });
    }

    render(){
        return(
            <>
             <Popup 
                open={this.state.messagePopup}
                contentStyle={{height: "auto"}}
                onClose={() => this.setState({messagePopup: false})}>
                    <h2 style={{color: this.state.submitColor, fontSize: "150%"}}>
                        {this.state.popupMessage}
                    </h2>
            </Popup> 
            <Auxiliary id="page-container">
                <Nav />
                <div className="content-wrap" style={{}}>
                    <h1 className={menuClass.h1} style={{paddingBottom: "3%"}}>Engineer Setup Form</h1>
                    {/* <h2 style={{color: this.state.submitColor, fontSize: "300%", height: "8%"}}>
                        {this.state.popupMessage}
                    </h2> */}
                    <Spacer height="11%" />

                    <form className="tab-content" style={{alignContent: "center", display: "table", marginLeft: "1%", zIndex:"100"}}>
                        <div style={{ display: "table-row"}}>
                            <label className={menuClass.addBlock}  style={{fontSize: "200%", display: "table-cell"}}>
                                Engineer ID:
                                
                            </label>
                            <input  style={{fontSize: "200%", display: "table-cell"}}
                                    type="number" name="id" value={this.state.engNo || ""} 
                                    onChange={(event) => this.inputHandler(event, "id")}/>
                        </div>
                        <div style={{ display: "table-row"}}>
                            <label className={menuClass.addBlock} style={{fontSize: "200%", display: "table-cell"}}>
                                Engineer Name:
                                
                            </label>
                            <input  style={{fontSize: "200%", display: "table-cell"}}
                                    type="text" name="name" value={this.state.engName || ""} 
                                    onChange={(event) => this.inputHandler(event, "name")}/>
                        </div>
                        <div style={{display: "table-row"}}>
                            <label className={menuClass.addBlock} style={{fontSize: "200%", display: "table-cell"}}>
                                Email Address:
                            
                            </label>
                            <input  style={{fontSize: "200%", display: "table-cell"}}
                                    type="email" name="email" value={this.state.engEmail || ""} 
                                    onChange={(event) => this.inputHandler(event, "email")}/>
                        </div>
                        <Spacer height='20%'/>      
                    </form>

                    <button 
                        style={{width: "50%", height: "auto"}} type="submit" value="Submit" 
                        onClick={this.submitHandler} >
                            Submit
                    </button>
                    
                    <div style={{height: "20%", marginTop:"5%"}}>
                    <button 
                        style={{width: "40%", height: "auto", fontSize: "150%", borderRadius: "0%", backgroundColor: "#99a1cf"}} 
                        onClick={this.uploadHandler} >
                            Manual upload
                    </button>
                    
                    <button 
                        style={{width: "40%", height: "auto", fontSize: "150%", borderRadius: "0%", backgroundColor: "#99a1cf"}} 
                        onClick={this.cleanupHandler} >
                            Storage Cleanup
                    </button>
                    </div>
                    <Footer history={this.props.history} />
                </div>
            </Auxiliary>
            </>
        );
    }
}

export default withRouter(Setup);

