import React, {Component} from 'react';


import Spacer from '../../UI/Spacer/Spacer';
import Nav from '../../UI/Nav/Nav';
import Footer from '../../UI/Footer/Footer'

//this page displays if there is a submission that hasn't made it to the server yet
class ErrorPage extends Component{

render(){
    return(
        <>
        <div style={{height: "100%"}}>
            <Nav />
            <Spacer height="25%"/>
            <h2 style={{color: "#eb4034", fontSize: "300%"}}>Cannot start a new entry -</h2>
            <h2 style={{color: "#eb4034", fontSize: "300%"}}>Last submission pending upload.</h2>
            <Spacer height="30%"/>
            <Footer history={this.props.history} />
        </div>
        </>
    );
}


}

export default ErrorPage;