import React,{Component} from 'react';
// import ReactTable from 'react-table-6';


import Auxiliary from '../../../containers/hoc/Auxiliary/Auxiliary'
import '../../Menu/Menu.module.css'
import Spacer from 'react-spacer';
import {/*Styles,*/ Table} from '../../../containers/Table/Table'
import '../../../containers/Table/Table.css'
  
  const columns = [     
        {
        Header: "ID",
        accessor: "id"
        },
        {
        Header: "Description",
        accessor: "desc",
        style: { 
            // whiteSpace: 'unset', 
            overflowWrap: "break-word" 
            }
        },
        {
            Header: "Supplier ID",
            accessor: "supplyID",
            style: { 
            // whiteSpace: 'unset', 
            overflowWrap: "break-word" 
            }
        },
        {
            Header: "Quantity",
            accessor: "qty"
        }
        
    ];


class EntryTables extends Component {

    state = {
        data: this.props.data
    }

    static getDerivedStateFromProps(nextProps, state){
        let tempData = [...nextProps.data];
        if(tempData!==state.data){
            let data= tempData;
            const newData = data.map(d=>({...d}));
          return {data: newData};
        }

       else return null;
    }

    render() {
        if(!this.state.data.length){
            return <div><Spacer height="20%"/><h1 >No Equipment Entered</h1></div>;
        }
        else return(
                <Auxiliary id="entry" className="container tab-pane active" >
                    <h3 style={{fontSize: "180%", marginBottom:"1%"}}>Hold press on any equipment to edit or delete</h3>
                    <Auxiliary className="table-wrapper"  >
                        <Spacer width = "20%"/>
                        <Table className="fl-table" data={this.state.data} columns={columns} editRow={this.props.editRow} /> 
                        <Spacer height="50%"/>
                    </Auxiliary>   
                </Auxiliary>

        );
    }

}

export default EntryTables;