// var PouchDB = require('pouchdb').default;
import PouchDB from 'pouchdb';
PouchDB.plugin(require('pouchdb-upsert'));

//init database
var OfflineDB = new PouchDB('engineer.stock.recon');

//ids of different documents =
export const equipEntries = 'equipEntries'; //the ongoing equipment list 
export const setup = 'setup'; //setup info
export const finalSub = 'finalSubmission'; //the submission pending transfer to google drive
export const toUpload = 'toUpload'; //the xml pending transfer to google drive


//////////////////CRUD FNS//////////////
//#region 
//generic read function 
export const getDoc = (id) => {
    try {
        return OfflineDB.get(id, {attachments: true})
        .catch(function(err){
            if (id !== equipEntries || id !== 'toUpload') console.log(err);
            return null;
        });
    } catch (err) {
        console.log(err);
        return null;
    }
}

//create / update
export const createOrUpdateDoc = (docIn, callbackFn) => {

    OfflineDB.put(docIn)
        .then(function(result){
            console.log(result);
            return result;
        })
        .catch(function(err){
            if (err.status === 409){
                OfflineDB.get(docIn._id)
                .then(function(doc){
                    docIn["_rev"] = doc._rev;
                     OfflineDB.put(docIn)
                    .then(function(result){
                        if(callbackFn){
                            callbackFn(result);
                        }
                        
                    })
                    .catch(function(err){
                        console.log(err);
                        if(callbackFn){
                            callbackFn(null);
                        }
                    }); 
                });
            }
            else{
                console.log(err);
            }
        });   
   
}

//generic delete document
export const resetDoc = (id) => {
    try{

        OfflineDB.get(id)
        .then(function(doc){
            OfflineDB.remove(doc);
        })
        .catch(function(err){
            console.log(err);
        }); 
    }catch(err){
        console.log(err);
    }
}
//#endregion
////////////////////////////////////////

//shapes document and creates or updates
export const formatAndPut = (tableName, data) => {   

    try{

        let docIn = {
            _id: tableName,
            data
        }
    
        createOrUpdateDoc(docIn)
        .then(function(result){
            console.log(result);
            return result;
        });

    }catch(err){
        return err;
    }
      
}

//add equipment items from array for autofill functionality
export const equipmentDb = (data) => {
    
    try{
        data.map(function(item){
            return OfflineDB.get(item.id)
                .catch(function(err){
                    OfflineDB.putIfNotExists({
                        _id: item.id,
                        desc: item.desc
                    }).catch(function(err){
                        console.log(err);
                    })
                });
        })    

    }catch(err){
        console.log(err);
    }
}

//cleanup function to remove redundant deleted fns
export const cleanup = (callbackFn) => {
    let 
        tempDB = new PouchDB('tempDB', {adapter: 'idb', size: 50}),
        docList = [],
        errorHandling = function(err){
            console.log(err);
            //callback(null, err);
        };
        //transfers records
    const dataTransfer = (dbFrom, dbTo) => {

            dbFrom.allDocs({ 																															
                include_docs: true,
                attachments: true
            }).then(function(result){
                
                result.rows.forEach(function(doc){
                    docList.push(doc.doc);
                });
                dbTo.bulkDocs(docList, {new_edits: false})
                .then(function(result){
                    //since doesn't return result - alldocs to verify docs are present
                    dbTo.allDocs()
                        .then(function(result){
                            if (result.rows.length > 0) { return result; }
                            else{ errorHandling("bulkDocs failed"); }
                        });	
                }).catch(function(err){
                    console.log(err);
                });

            }).catch(function(err){
                errorHandling(err);
            });
        }

    Promise.resolve(dataTransfer(OfflineDB, tempDB))
    .then(function(result){
        OfflineDB.destroy()
            .then(function(result){
                Promise.resolve(OfflineDB = new PouchDB('engineer.stock.recon'))
                .then(function(result){
                    Promise.resolve(dataTransfer(tempDB, OfflineDB))
                    .then(function(result){  
                        tempDB.destroy()
                        .then(function(result){
                            if(callbackFn){
                                callbackFn(result);
                            }
                            else return result;
                        });  
                    });
                });
                
            });
    });

    
                                
                                                                            
}

