import React,{Component} from 'react'
// import footerClass from './Footer.css'
// import NavClass from '../Nav/Nav.css'
// import gearimg from '../../../assets/images/gears.png';

class Footer extends Component {
    render(){
        return(
            <div className="footer" style={{height: "10%", zIndex: "-1"}}>
                <button style={{ width: "auto", height: "auto", float: "left", marginTop: "1%", borderRadius:"5%"}} onClick={() => this.props.history.push('/')}>
                    Back
                    {/* <img style={{height: "90%"}} src={gearimg}/> */}
                </button>
            </div>
        );
    }
}

export default Footer;