import React, { Component} from 'react';
import {  withRouter } from 'react-router-dom';
import Popup from 'reactjs-popup';

import menuClass from './Menu.module.css';
import buttonClass from '../UI/Button/Button.css';
// import footerClass from '../UI/Footer/Footer.css'

// import logoClass from '../Logo/Logo.module.css';
import Spacer from '../UI/Spacer/Spacer';
import spacerClass from '../UI/Spacer/Spacer.css'

import Nav from '../UI/Nav/Nav';
// import Footer from '../UI/Footer/Footer'
// import navClass from '../UI/Nav/Nav.css'
// import Auxiliary from '../../containers/hoc/Auxiliary/Auxiliary';
import * as dbActions from '../DB/Db';

import gearimg from '../../assets/images/gears.png';


class Menu extends Component {
    state = {
        popupBool: false,
        data: [],
        submitMessage: "",
        submitColor: "green"
    }

    componentDidMount(){
        let that = this;
        //get entries made but not yet submitted
        dbActions.getDoc(dbActions.equipEntries)
        .then(function(doc){
            that.setState({data: doc ? doc.data: []});
        })
        .catch(function(err){
            that.setState({data: []});
            console.log(err);
        });

        dbActions.getDoc(dbActions.finalSub)
        .then(function(result){
          if(result){
            that.props.manualRun(); //GOOGLE DRIVE
          }
        });

    }


    //confirm data to be sent to server
    finalSubmit = () => {
         
        const that= this, triggerFail = (message) => {
            this.setState({popupBool: true, submitMessage: message, submitColor: "#a84232"});
        }

        if (that.state.data.length > 0){
            dbActions.getDoc(dbActions.setup) //verify engineer details are present
            .then(function(result){
                console.log(result);
                if (result.engNo === "" || result.engName === "" || result.engEmail === ""){
                    triggerFail("Incomplete engineer details");
                }
                else{
                    dbActions.formatAndPut(dbActions.finalSub,that.state.data);//submit to db
                    dbActions.equipmentDb(that.state.data); //add to equipment history
                    dbActions.resetDoc(dbActions.equipEntries); //reset the unsubmitted info
                    that.setState({popupBool: true, data: [], submitMessage: "Equipment Submitted.", submitColor: "#15a12f"});
                    that.props.manualRun(); //GOOGLE DRIVE
                }
            })
            .catch(function(err){
                triggerFail("Please enter engineer details in settings menu");
            });
           
        }else{
            triggerFail("No Equipment to submit.");
        }
       
    }

    render(){       
        //className="center-fit"
        return(
            <div className={menuClass}  style={{height: "64%"}}>
                <Popup 
                    open={this.state.popupBool}
                    position="top center" 
                    contentStyle={{height: "auto"}}
                    onClose={() => this.setState({popupBool: false})}>
                        <h2 style={{color: this.state.submitColor, fontSize: "150%"}}>
                            {this.state.submitMessage}
                        </h2>
                </Popup>   
                <Nav />
                <Spacer height='24%'/>
                <div style={{height: "100%", alignContent: "center"}}>    
                    <button 
                        className={buttonClass} 
                        style={{height: "25%", width:"75%", fontSize: "400%", borderRadius: "0%"}} 
                        onClick={() => this.props.history.push('/new-entry')}>
                            Edit Entry {'>'}
                    </button>
                    <Spacer style={{display: "table-cell"}} height="16%" className={spacerClass.spacer}/>
                    <button 
                        className={buttonClass} 
                        style={{height: "25%", width:"75%", fontSize: "400%", backgroundColor: "#a83a32", borderRadius: "0%"}} 
                        onClick={this.finalSubmit}>
                            Submit 
                    </button>
                </div>
                <div className="footer">
                    <button 
                        style={{backgroundColor: "white", width: "auto", height: "84%", float: "right", marginTop:"1%", 
                        borderRadius: "10%"}} 
                        onClick={() => this.props.history.push('/setup')}>
                            <img style={{height: "60%", float: "center", marginTop: "-3vh"}} alt="settings" src={gearimg}/>
                            <p style={{color: "black", fontSize: "25%", marginTop: "-2vh"}}>Settings</p>
                    </button>
                </div>
            </div>
        );
    }
}

export default withRouter(Menu);
