    // Request to server to get Access token from default refresh token
    export const requestAccessToken = ( callback ) => {
        var url = 'authenticate.php';
            // url = ( window.navigator.platform === "Win32" || "MacIntel" ) ? "authenticate.php" : "https://britton-price-service-docs.appspot.com/authenticate.php";
        console.log(url);

        axios.get( url, {
                params:{
                    refresh: true
                }
            }
        ).then(function(response){
            console.log(response);
            callback(response);
        }).catch(function(error){
            if (error.response) {
                // The request was made and the server responded with a status code
                // that falls out of the range of 2xx
                console.log(error.response.data);
                console.log(error.response.status);
                console.log(error.response.headers);
              } else if (error.request) {
                // The request was made but no response was received
                // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                // http.ClientRequest in node.js
                console.log(error.request);
              } else {
                // Something happened in setting up the request that triggered an Error
                console.log('Error', error.message);
              }
            console.log(error.config);
            callback(error);
        });
    }

    // Load gapi client - v3
    export const handleClientLoad = (callback) => {
        gapi.load('client:auth', callback);
    }

    // Request to set Access token with supplied key
    export const setAccessToken = ( token ) => {
        try{
            gapi.client.setToken(token);                                                            // v3
        }catch(err){
            console.log(err);
        }
    }

    // Load Drive API
    export const loadDriveApi = (token, callback ) => {
        // v3
        gapi.client.init({
            discoveryDocs: DISCOVERY_DOCS
        }).then(function(){
            setAccessToken(token);
            callback();
        })
        .catch(function(err) {
            callback(err);
        })
    }

    //#region   // Get version file (Version.json - located in root folder, ServiceDocs
    export const getAppVersion = ( callback ) => {
        var folderId,
            inFile;

        findFolder( "ServiceDocs", folderId, function ( result ) {
            // Callback from googleApi.findFolder
            if ( result.error ) {
                // Error - abandon
                callback( { error: { message: "Unable to retrieve root folder \"ServiceDocs\".<br><br> Code " + result.error.code + "  " + result.error.message } }, 0 );
                return;
            } else {
                // Get version file
                folderId = result[ 0 ].id;
                findFiles( "Version.json", folderId, function ( result ) {
                    // Returns an array of files found (should only be one)
                    if ( result.error ) {
                        if ( result.error.code === 404 ) {
                            // Version file not found - return empty string
                            callback( "" );
                        } else {
                            callback( { error: { message: "Unable to retrieve file version.<br><br> Code: " + result.error.code + "  " + result.error.message } } );
                        }
                        return;
                    } else {
                        // Got version file - result contains object list of file details
                        if ( result.length === 0 ) {
                            // Version file not found
                            callback( "" );
                        } else {
                            // Get version number from first file in list
                            downloadFile( result[ 0 ], function ( result ) {
                                if ( result.error ) {
                                    callback( { error: { message: "Unable to download version file.<br><br> Code: " + result.error.code + "  " + result.error.message } } );
                                } else {
                                    try {
                                        inFile = JSON.parse( result );
                                        inFile = inFile[0].result;                                          // v3
                                        callback( inFile.Version );
                                    }
                                    catch ( err ) {
                                        callback( { error: { message: "Error in processing Version file \"" + result[ 0 ].title + "\" - please advise head office.<br><br>Error: " + err.message } } );
                                    };
                                };
                            } );
                        };
                    };
                } );
            };
        } );
    }

    // Retrieve folder using supplied folder name as the root for the search
    export const findFolder = ( folderName, parentId, callback ) => {
        var //me = this,
            fields = "files(id,kind,mimeType,parents,name)",                                                // v3
            search,
            request;

        if ( gapi.client.drive === undefined ) {
            // No connection
            callback( { error: { code: 503, message: 'Google gapi client not available' } } );
            return;
        } else {
            search = "mimeType='application/vnd.google-apps.folder'";
            if ( parentId ) {
                search += " and '" + parentId + "' in parents"
            } else {
                search += " and 'root' in parents"
            };
            //search += " and title='" + folderName + "' and trashed=false";                                // v2
            search += " and name='" + folderName + "' and trashed=false";                                   // v3
            request = gapi.client.drive.files.list( {
                'q': search,
                'fields': fields
            } );
            console.log(request);
            request.execute( function ( result ) {
                if ( result.error !== undefined ) {
                    // Not found
                    callback( result );
                } else {
                    //callback( result.items );                                                             // v2
                    callback( result.files );                                                               // v3
                };
            } )
        }
    }

    // Create folder
    export const createFolder = ( folderName, parentId, callback ) => {

        // v3
        var createRequest,
            metadata = {
                name: folderName,
                parents: [ parentId ],
                mimeType: 'application/vnd.google-apps.folder'
            };

        if ( parentId === undefined ) {
            metadata = {
                name: folderName,
                mimeType: 'application/vnd.google-apps.folder'
            };
        };
        createRequest = gapi.client.drive.files.create( {
            resource: metadata,
            fields: 'id'
        });
        createRequest.execute(function(file) {
            callback(file);
        });
    }

    // Find file(s) - will be needed if appversion is found 
    export const findFiles = ( fileName, parentId, callback ) => {
        var //me = this,
            //fields = "items(description,id,kind,mimeType,parents,title,modifiedDate,downloadUrl)",           // v2 Metadata to download in result.items object
            fields = "files(id,kind,mimeType,parents,name,modifiedTime,webContentLink)",           // v3 Metadata to download in result.files object
            //fields = "*",
            // orderBy = 'modifiedTime',
            search,
            initialRequest,
            nextPageToken;

        var compare = function ( a, b ) {
            // Sort files downloaded from Google Drive into same order in which they were uploaded
            //if ( a.modifiedDate > b.modifiedDate ) return 1;                                              // v2
            //if ( b.modifiedDate > a.modifiedDate ) return -1;                                             // v2
            if ( a.modifiedTime > b.modifiedTime ) return 1;                                                // v3
            if ( b.modifiedTime > a.modifiedTime ) return -1;                                               // v3
            return 0;
        }

        var retrievePageOfFiles = function ( request, result ) {
            request.execute( function ( resp ) {
                if ( !resp || resp.error ) {
                    if ( result.length === 0 ) {
                        // Nothing found
                        callback( { error: { message: "No files found.", code: 404 } } );
                    } else {
                        callback( resp );
                    };
                } else {
                    // Concatenate results
                    //result = result.concat( resp.items );                                                 // v2
                    result = result.concat( resp.files );                                                   // v3
                    nextPageToken = resp.nextPageToken;
                    if ( nextPageToken ) {
                        request = gapi.client.drive.files.list( {
                            'q': search,
                            'fields': fields,
                            //'orderBy': orderBy,
                            'pageToken': nextPageToken
                        } );
                        // Get next page of files
                        retrievePageOfFiles( request, result );
                    } else {
                        // Sort array into modified date order
                        result.sort( compare );
                        // Return results
                        callback( result );
                    }
                }
            } );
        }

        // Set-up initial Google Drive request
        if ( gapi.client.drive === undefined ) {
            // No connection
            callback( { error: { code: 503, message: 'Google gapi client not available' } } );
            return;
        } else {
            search = "mimeType != 'application/vnd.google-apps.folder'";
            if ( parentId ) {
                search += " and '" + parentId + "' in parents"
            } else {
                search += " and 'root' in parents"
            };
            if ( fileName ) {
                // Filter on supplied file name
                //search += " and title='" + fileName + "'";                                                // v2
                search += " and name='" + fileName + "'";                                                   // v3
            };
            search += " and trashed=false";
            initialRequest = gapi.client.drive.files.list( {
                'q': search,
                'fields': fields
                //'orderBy': orderBy
            } );
            retrievePageOfFiles( initialRequest, [] );
        }
    }

    // Insert new file to folder
    export const insertFile = ( fileName, data, parentId, callback ) => {

        // v3
        var boundary = '-------314159265358979323846',
            delimiter = "\r\n--" + boundary + "\r\n",
            close_delim = "\r\n--" + boundary + "--",
            contentType = 'application/xml',
            metadata = {
                name: fileName,
                parents: [ parentId ],
                mimeType: contentType
            },
            base64Data = btoa( unescape( encodeURIComponent( data ) ) ),
            multipartRequestBody =
                delimiter +
                'Content-Type: application/json\r\n\r\n' +
                JSON.stringify( metadata ) +
                delimiter +
                'Content-Type: ' + contentType + '\r\n' +
                'Content-Transfer-Encoding: base64\r\n' +
                '\r\n' +
                base64Data +
                close_delim;

        var request = gapi.client.request( {
            'path': 'upload/drive/v3/files',
            'method': 'POST',
            'params': { 'uploadType': 'multipart' },
            'headers': {
                'Content-Type': 'multipart/mixed; boundary="' + boundary + '"'
            },
            'body': multipartRequestBody
        } );

        request.execute( callback );

    }

    // Download file contents
    export const downloadFile = ( fileItem, callback ) => {

        // v3
        var //me = this,
            getRequest;
        
        getRequest = gapi.client.drive.files.get( {
            'fileId': fileItem.id,
            'alt': 'media',
            'mimeType': fileItem.mimeType
        });
        getRequest.execute(function(errObj, content) {
            if (errObj.error) {
                // Error
                callback(errObj);
            } else {
                // Return content string
                callback(content);
            };
        })

    }

    ///////////////GOOGLE CONNECTION/////////////////
    //#region 

    export const makeGoogleConnection = ( callbackFunc ) => {

        var retries = 0,
            httpHeaders,
            errMsg = 'Authorisation has failed - unable to upload forms.';

        // Get Access token from our own server which uses offline mode and a refresh token
        var v_requestAccessToken = () => {
            requestAccessToken( function ( result ) {
                if ( result == null ) {
                    // Failed - give up
                    callbackFunc( { error: { message: "1) " + errMsg } } )
                    return;
                } else {
                    if ( result.error !== undefined ) {
                        // Other error - abandon upload
                        callbackFunc( { error: { message: "2) " + errMsg + "<br><br> Code " + result.error.code + "  " + result.error.message } } );
                        return;
                    } else {
                        if ( result.status !== 200 ) {
                            // Status error - check for code 0: authorisation failed
                            if ( result.status === 0 ) {
                                // Authorisation failed - retry once
                                if ( retries === 0 ) {
                                    retries += 1;
                                    requestAccessToken();
                                    return;
                                }
                            };
                            callbackFunc( { error: { message: "3) " + errMsg + "<br><br> Status " + result.status + "  " + result.statusText } } );
                            return;
                        }
                        // Got the access token -> set it
                        try {

                            httpHeaders = result.headers;
                            if ( httpHeaders === undefined ) {
                                // Invalid response - abandon upload
                                callbackFunc( { error: { message: "4) " + errMsg + "<br>" + result.responseText } } );
                                return;
                            } else {
                                if ( !gapi ) {
                                    var gapiClient = document.createElement( 'script' );
                                    gapiClient.src = 'https://apis.google.com/js/api.js';                                       // v3
                                    gapi = document.body.appendChild( gapiClient );
                                };

                                const authToken = JSON.parse(httpHeaders.response);
                                
                                // v3
                                handleClientLoad(function() {
                                    loadDriveApi( authToken, callbackFunc );
                                });
                            }
                        } catch ( err ) {
                            callbackFunc( { error: { message: "5) " + errMsg + "<br><br>" + err } } );
                            return;
                        };
                    };
                };
            } );
        };

        v_requestAccessToken();
    }
