import React, {Component} from 'react';
import BarcodeReader from 'react-barcode-reader'

// import Tabs from 'react-bootstrap/Tabs'
// import Tab from 'react-bootstrap/Tab'
// import classTab from '../UI/Tabs/Tabs.css'
// import menuClass from '../Menu/Menu.module.css'
// import buttonClass from '../UI/Button/Button.css'
import * as dbActions from '../DB/Db'

import qrImg from '../../assets/images/scan-qr-code-icon-21.jpg' 
import Auxiliary from '../../containers/hoc/Auxiliary/Auxiliary'
import Nav from '../UI/Nav/Nav';
import EntryForm from './EntryForm/EntryForm';
import EntryTables from './EntryTables/EntryTables'
import Popup from "reactjs-popup";
import Numpad from '../UI/numpad/Numpad'
// import Spacer from '../UI/Spacer/Spacer';
import QR from '../QR/qr'
import Footer from '../UI/Footer/Footer'
import '../UI/Scrollbar/Scrollbar.css'

//numpad window
const styling = {
    width: "70%",
    height: "79%",
    offsetY: "-100vh",
    offsetX: "-200vh" 
}

//qr window
// const qrStyling = {
//     backgroundColor: '#ffffff',
//     height: '5%',//'10%',
//     // width: 'auto',
//     marginBottom: '5%'
// };

class NewEntry extends Component {
    
    state = {
        // input values 
        id: "",
        desc: "",
        supplyID: "",
        qty: "0",
        //UI popup triggers
        numpadPopup: false,
        qrPopup: false,
        errorPopup: false,
        errorMessage: "",
        //scan functionality 
        barcodeScans: [],
        //is row being edited
        editRow: "",
        //table data
        data: []
    }


    componentDidMount(){
        let that = this;
        //get entries made but not yet submitted
        dbActions.getDoc(dbActions.equipEntries)
        .then(function(doc){
            if(doc.data){  
                that.setState({data: doc.data});
            }
        })
        .catch(function(err){
            console.log(err);
        });
    }

    //QR
    //#region 
    //trigger open QR camera window
    openQR = () => { 
        this.setState({qrPopup: true});
    }
    //trigger close QR camera window
    closeQR = () => { 
        this.setState({qrPopup: false});
    }
    //#endregion

    // actively updates states of input in case of text entry 
    inputHandler = (event, inId) => {
        switch(inId){
            case "id":
                this.setState({id: event.target.value});
                break;
            case "desc":
                try{
                    this.setState({desc: event.target.value});
                }catch(err){
                    this.setState({desc: event});
                }
                break; 
            case "supplyID":
                try{
                    this.setState({supplyID: event.target.value});
                }catch(err){
                    this.setState({supplyID: event});
                }
                break;  
            default: break;
        }
    }

    //trigger opening of number pad popup
    openPop = () => {
        this.setState({numpadPopup: true});
    }

    //data scan via qr or barcode
    scanInput = (props) => { 

        let splIn = props.split('\n');
        const that = this,
            behaviour = function(){
                if(splIn[1] == null){ //read by barcode reader
                    that.state.barcodeScans.push(splIn[0]);
                    that.setState({id: that.state.barcodeScans[0], desc: that.state.barcodeScans[1], supplyID: that.state.barcodeScans[2]});
                }
                else{ //read by device camera
                    that.setState({id: splIn[0], desc: splIn[1], supplyID: splIn[2]});
                }

                //quantity input window
                that.openPop();
            };

        //if read through camera qr, all inputs are passed at once. If read w/ hardware barcode reader, they are passed consecutively, thus pushed to temp array.
        behaviour();

    }

    //add equipment to table for submission
    closePopAndSubmit = (qtyIn) => {  
        //if editing an existing row, not adding a new one
        if(this.state.editRow !== ""){
            let tempArray = [...this.state.data]; 
            const tempValue = {
                id: this.state.data[this.state.editRow].id,
                desc: this.state.data[this.state.editRow].desc,
                supplyID: this.state.data[this.state.editRow].supplyID,
                qty: qtyIn
            }
            tempArray[this.state.editRow]= tempValue;
            this.setState({data: tempArray});
        }else{
            this.state.data.push({id: this.state.id, desc: this.state.desc, supplyID:this.state.supplyID, qty: qtyIn});
        }


        //update pouchdb 
        dbActions.formatAndPut(dbActions.equipEntries, this.state.data);
        this.setState({ barcodeScans: [], numpadPopup: false, id: "", desc: "", supplyID:"", editRow: ""});
    }

    //edit existing row 
    editRow = (index) => {
        // set row to be edited as active and trigger numpad
        this.setState({
            id: this.state.data[index].id, 
            desc: this.state.data[index].desc, 
            supplyID: this.state.data[index].supplyID, 
            qty: this.state.data[index].qty, 
            numpadPopup: true, 
            editRow: index});
    } 

    //delete existing row
    deleteRow = () => {
        let tempArray = [...this.state.data]; 
        tempArray.splice(this.state.editRow,1);

        //update pouchdb 
        dbActions.formatAndPut(dbActions.equipEntries,tempArray);
        this.setState({data: tempArray, numpadPopup: false, editRow: "", id: "", desc: "", supplyID: ""});
    }


    render () {
        //output of data entry screen - input form and table are external components. Popup functionality stored here 
        return(
            <Auxiliary className="content">
                <Popup  
                    open={this.state.errorPopup}
                    onClose={() => this.setState({errorPopup: false})}>
                    {this.state.errorMessage}
                </Popup>
                 <Popup 
                    open={this.state.numpadPopup}
                    // position="top center" 
                    contentStyle={styling}
                    onClose={() => this.setState({numpadPopup: false, editRow: "", qty: "", id: null, desc: null})}>
                        <Auxiliary>
                            <p style={{fontSize: "200%"}}>
                                <strong style={{fontWeight: "bold", color: "black"}}>
                                    ID:
                                </strong> 
                                {this.state.id || "\t"}
                                <strong style={{fontWeight: "bold", color: "black"}}>
                                    Desc:
                                </strong> 
                                {this.state.desc || "\t"}
                                <strong style={{fontWeight: "bold", color: "black"}}>
                                    Supplier ID:
                                </strong> 
                                {this.state.supplyID || "\t"}
                            </p>
                            <Numpad 
                                close={this.closePopAndSubmit} 
                                qty={this.state.qty} 
                                editRow={this.state.editRow}
                                deleteRow={this.deleteRow}/>
                        </Auxiliary>
                </Popup>
                <Nav style={{height: "5%"}}/>
                <BarcodeReader onScan={this.scanInput} />
                <div style={{height: "35%", paddingTop: "1%", paddingBottom: "5%"}} >
                    <EntryForm 
                        id={this.state.id} desc={this.state.desc} supplyID={this.state.supplyID} qty={this.state.qty}  
                        open={this.openPop} 
                        inputHandler={this.inputHandler}/>    
                   
                    <button 
                        // style={qrStyling} 
                        style={{ backgroundColor: "#f5f5f5", backgroundSize:"100% 100%", height: "auto%", width:"20%", justifyContent: "center"}} 
                        onClick={this.openQR}>
                            <img src={qrImg} alt="openCamera" style={{ height: "110%", width:"100%", objectFit:"fill", backgroundSize:"100% 100%"}} /> 
                    </button>
                    <button 
                        style={{height: "40%", width:"20%", justifyContent: "center"}}
                        onClick={this.openPop}>
                            <p style={{ fontSize: "50%", }}>Add Qty</p>
                    </button>
                    <Popup 
                        open={this.state.qrPopup}
                        position="left top" 
                        contentStyle={styling}
                        onClose={this.closeQR}>
                            <QR scanIn={this.scanInput} scanClose={this.closeQR} qtyIn={this.openPop}/>
                    </Popup>
                </div>
                <div style={{height: "45%", width: "100%"}}>
                    <EntryTables style={{marginTop:"5%", height: "100%"}} data={this.state.data} editRow={this.editRow}/>
                </div> 
                <Footer history={this.props.history} />
            </Auxiliary>     
 
        );
    }
}

export default NewEntry;