import React,{Component} from 'react';

import Auxiliary from '../../../containers/hoc/Auxiliary/Auxiliary'
import  '../../UI/Form/Form.css';
// import tabClass from '../../UI/Tabs/Tabs.css'
import  * as dbActions from '../../DB/Db';


class EntryForm extends Component {

    state = {
        id: this.props.id,
        desc: this.props.desc,
        supplyID: this.props.supplyID,
        qty: 0,
        qtyPopup: false
    }

    //update on change of info
    static getDerivedStateFromProps(nextProps, state){
        if(nextProps.id!==state.id || nextProps.desc!==state.desc || nextProps.supplyID!==state.supplyID){
            let newId= nextProps.id,
                newDesc= nextProps.desc,
                newSupplyID = nextProps.supplyID;
          return {id: newId, desc: newDesc, supplyID: newSupplyID}; 
       }
       else return {id: state.id, desc: state.desc, supplyID: state.supplyID};
    }

 

    //checks if there has been a previous entry with the same ID - if so returns and autofills description
    autoFillCheck = (event, type) => {
        this.props.inputHandler(event, "id");
           
        const that = this;
        dbActions.getDoc(event.target.value)
            .then(function(doc){
                if(doc){
                    that.setState({desc: doc.desc});
                    that.props.inputHandler(doc.desc, "desc");
                    that.props.inputHandler(doc.supplyID, "supplyID");
                }
            }).catch(function(error){
                console.log("ignore errors here", error);
            });
    }

    render(){
        return(
            <Auxiliary id="entry"  > 
                <div  style={{zIndex: "1", paddingTop:"2%", width: "70%", float: "left"}}> 
                    <form style={{display: "table", alignContent: "center", width: "100%", margin: "5%"}}>
                        <div style={{alignContent: "left", display: "table-row"}}>
                            <label style={{fontSize: "200%", display: "table-cell"}}>
                                ID:
                            </label>  
                            <input 
                                type="text" 
                                style={{width: "50%", fontSize:"150%", display: "table-cell"}} //, marginLeft:"10%"
                                value={this.state.id || ""}  
                                onChange={(event) => this.autoFillCheck(event)}/>  
                        </div>
                        <div style={{ display: "table-row"}}>
                            <label style={{fontSize: "200%", display: "table-cell"}}>
                                Desc:
                            </label> 
                            <input
                               type="text" 
                                style={{width: "50%", fontSize:"150%", display: "table-cell"}} 
                                value={this.state.desc || ""}  
                                onChange={(event) => this.props.inputHandler(event, "desc")}/>
                        </div>
                        <div style={{ display: "table-row"}}>
                            <label style={{fontSize: "200%", display: "table-cell"}}>
                                Supplier ID:
                            </label> 
                            <input
                               type="text" 
                                style={{width: "50%", fontSize:"150%", display: "table-cell"}} 
                                value={this.state.supplyID || ""}  
                                onChange={(event) => this.props.inputHandler(event, "supplyID")}/>
                        </div>
                    </form>
                </div>
                
            </Auxiliary>
        );
    }
}

export default EntryForm;