import React, {Component} from 'react';


const buttonStyling = {
    width: "20%",
    height: "75%",
    padding: "0",
    fontSize: "350%"

};

class Numpad extends Component {


    state= { 
        qtyVal : this.props.qty, 
        button: null //delete button if editing an existing piece of equipment
    }

    componentDidMount(){
        if(this.props.editRow !== ""){
            this.setState({button: <button style={{fontSize: "auto", width: "30%", height:"auto", backgroundColor: "red"}} onClick={this.props.deleteRow}>Delete</button>}); 
        }
    }
    //click on enter 
    enterHandler = () => {
        this.props.close(this.state.qtyVal);
    }

    //click on 0-9
    addNum = (props) => {
        if (this.state.qtyVal === "0"){
            this.setState({qtyVal: props});
        }
        else this.setState({qtyVal: this.state.qtyVal + props});
    }

    //click on C to cancel last character
    removeChar = () => {
        if (this.state.qtyVal.length <= 1 || this.state.qtyVal === "0"){
            this.setState({qtyVal: "0"});
        }
        else {
            this.setState({qtyVal: this.state.qtyVal.slice(0, -1)});
        }
    }

    render() {
        return (
            <div style={{alignText: "center", display: "block"}}>
                <label style={{padding:"2%", display: "block", fontSize: "200%"}} htmlFor="qty">Qty:
                    <input readOnly type="number" pattern="[0-9]*" value={this.state.qtyVal} id="qty" />
                </label>
                <div >
                    <div style={{height:"16%"}}>
                        <button style={buttonStyling} onClick={() => this.addNum("7")}>7</button>
                        <button style={buttonStyling} onClick={() => this.addNum("8")}>8</button>
                        <button style={buttonStyling} onClick={() => this.addNum("9")}>9</button>
                    </div>
                    <div style={{height:"16%"}}>
                        <button style={buttonStyling} onClick={() => this.addNum("4")}>4</button>
                        <button style={buttonStyling} onClick={() => this.addNum("5")}>5</button>
                        <button style={buttonStyling} onClick={() => this.addNum("6")}>6</button>
                    </div>
                    <div style={{height:"16%"}}>
                        <button style={buttonStyling} onClick={() => this.addNum("1")}>1</button>
                        <button style={buttonStyling} onClick={() => this.addNum("2")}>2</button>
                        <button style={buttonStyling} onClick={() => this.addNum("3")}>3</button>
                    </div>
                    <div style={{height:"16%"}}>
                        <button style={buttonStyling} onClick={() => this.addNum("0")}>0</button>
                        <button style={buttonStyling}  onClick={this.removeChar}>C</button>
                    </div>
                    <button type="submit" style={{width: "30%", height:"auto", backgroundColor:"#145201" }}  onClick={this.enterHandler}>OK</button>
                    {this.state.button}
                </div>
            </div>    
        );
    }    
}

export default Numpad;