import React, {Component} from 'react';
import * as googleConnection from './GoogleDriveAPI'
import * as googleApi from './GoogleDriveAPI'
import * as dbActions from '../components/DB/Db'
import { gapi } from 'gapi-script';
import Wrapper from '../containers/Wrapper/Wrapper';
import 'react-dom';


class GDrive extends Component {

    state = {
        callback: null,
        folderPath: "",
        folderId: "",
        fileName: 'stockInput.xml',
        xml: "",
        attachment: null,
        docIn: null,
        engineerID: "",
        activeSubmission: false,
        outFolderId: null,
        stockFolderId : null

    }

    ///ORIGIN: sendFSRFiles
    sendAllFiles = (callbackFn) => {

        let me = this,
        folders = ['notYourServiceDocs', me.state.engineerID, 'StockControl', 'Out'], //'ServiceDocs',
        folderId;
        // googleApi = google;
    
        //get to folder
        const findFolder = (folder, folderId) => {
            googleApi.findFolder(folder, folderId, function (result) {
                // Callback from googleApi.findFolder
                if (result.error || result.length === 0) {
                    if (!result.error) {
                        // Folder not found - create it
                        me.createFolders(folders, folderId, function(result){
                            callbackFn(result);
                        });
                    } else {
                        if (result.error.code === 404) {
                            // Folder not found - create it
                            me.createFolders(folders, folderId, function(result){
                                callbackFn(result);
                            });
                        } else {
                            // Other error - abandon upload
                            if(callbackFn){
                                callbackFn({ error: { message: "Unable to retrieve folder details.<br><br> Code: " + result.error.code + "  " + result.error.message } }, 0);
                            }
                            return;
                        };
                    };
                } else {
                    // Folder found - get next folder level or, if at required folder, process files
                    if (folders[0] === 'StockControl') me.setState({stockFolderId: result[0].id});
                    if (folders[0] === 'Out') me.setState({outFolderId: result[0].id});
                    folders.shift();
                    if (folders.length === 0) {
                        // All folders exist - process all files to be added to 'Out' folder
                        folderId = result[0].id;
                        me.uploadFile(function(result){
                            callbackFn(result);
                        });
                        // return;
                    } else {
                        folderId = result[0].id;
                        findFolder(folders[0], folderId);
                        return;
                    };
                };
            });
        };
        
        findFolder(folders[0], folderId);
    }

    //ORIGIN: sendFSRfiles
    // Create folders - param {folders} is an array of folder names to be created
    createFolders = (folders, parentId, callbackFn) => {
        var me = this;

        var createFolder = function (folder, parentId) {
            googleApi.createFolder(folder, parentId, function (result) {
                // Callback from googleApi.createFolder
                if (result.error) {
                    // Create folder failed
                    callbackFn({ error: { message: "Unable to create folder '" + folders[0] + "'.<br><br> Code: " + result.error.code + "  " + result.error.message } }, 0);
                    return;
                } else {
                    // Folder created - get next folder level to create or, if all created, process files
                    if (folders[0] === 'StockControl') me.setState({stockFolderId: result.id});
                    if (folders[0] === 'Out') me.setState({outFolderId: result.id});
                    folders.shift();
                    if (folders.length === 0) {
                        // All folders created - process all files to be added to 'Out' folder
                        parentId = result.id;
                        me.uploadFile(function(result){
                            callbackFn(result);
                        });
                        return;
                    } else {
                        // Create next folder as a sub-folder of the folder just created
                        parentId = result.id;
                        createFolder(folders[0], parentId);
                        return;
                    };
                };
            });
        };

        createFolder(folders[0], parentId);
    }


    //creates xml string of engineer details + stock data and puts in db 
    createUploadDoc = () => {
        
        //empty templates to be populated with info
        const xmlTemplate="<StockTake><EngineerID>{EngID}</EngineerID><EngineerName>{EngName}</EngineerName>{Product}</StockTake>",
              prodTemplate = "{Product}<Products><StockCode>{StockCode}</StockCode><Description>{Desc}</Description><SupplierID>{SupplyID}</SupplierID><Quantity>{Qty}</Quantity></Products>";
        
        let file = xmlTemplate,
            products = prodTemplate,
            engid = "",
            engname = "",
            productlist = [],
            that = this,
            fileName;
    
        try{
            //get engineer info
    
            dbActions.getDoc(dbActions.setup)
                .then(function(doc){
                    
                    engid = doc.engNo;
                    that.setState({engineerID: engid});
                    engname = doc.engName;
                    
                })
                .then(function(){
                    //get submission data 
                    dbActions.getDoc(dbActions.finalSub)
                    .then(function(doc){
                        productlist = [...doc.data];
                    })
                    .then(function(){
                        file = file.replace("{EngID}", engid);
                        file = file.replace("{EngName}", engname);
                        productlist.map(function(item){
                            products = prodTemplate;
                            products = products.replace("{StockCode}", item.id);
                            products = products.replace("{Desc}", item.desc);
                            products = products.replace("{Qty}", item.qty);
                            products = products.replace("{SupplyID}", item.supplyID);
                            file = file.replace("{Product}", products);
                            return file;
                        });
                        
                        file = file.replace("{Product}", "");
                        fileName = 'stockInput'+ new Date().toJSON() + Math.random() +'.xml';
                        //convert xml to base64
                        that.setState({
                            fileName: fileName,
                            xml: file, 
                            docIn:{
                                _id: dbActions.toUpload, 
                                docName: fileName,
                                _attachments:{
                                    'stockInput.xml': {
                                    content_type: 'text/plain',
                                    data: btoa(file)
                                    }
                                }
                            } 
                        });
                        
                    }).then(function(){     
                        //put xml string in pouchdb
                        dbActions.createOrUpdateDoc(that.state.docIn);
                    })
                    .catch(function(err){
                        //delete and reinstate xml if already exists
                        Promise.resolve(dbActions.resetDoc(dbActions.toUpload))
                        .then(function(){
                            dbActions.createOrUpdateDoc(that.state.docIn);               
                        })
                        .catch(function(err){
                            console.log(err);
                        })
                    })
                }).catch(function(err){
                    file = err;
                    console.log(err);
                });
    
    
        }catch(err){
            console.log(err);
        }
    }
    
    //fetch xml attachment from pouchdb and populate in state
    fetchUploadDoc = () => {

        let that = this;

        dbActions.getDoc(dbActions.toUpload) //fetch xml from pouch 
            .then(function(doc){
                let temp = doc._attachments['stockInput.xml'];
                that.setState({attachment: temp, xml: atob(temp.data), fileName: doc.docName}); //stockInput.xml placed in state
            }).catch(function(err){
                console.log(err);
        });
        
    }
    
    //ORIGIN: sendFSRfiles
    //sets connection to google drive and puts file 
    uploadFile = (callbackFn) => { 
        let that = this;
        try {
            // that.setState({folderId: folderID});
            googleConnection.insertFile(that.state.fileName, that.state.xml, that.state.outFolderId, function (result) {
                // Callback from googleApi.insertFile
                if (result.error) {
                    // Insert file failed - sync any updates before returning to calling process
                   console.log("Unable to submit file.<br><br> Code: " + result.error.code + "  " + result.error.message);
                    callbackFn("Unable to submit file.<br><br> Code: " + result.error.code + "  " + result.error.message);
                } else {
                    // Insert successful
                    callbackFn(true);
                    // return true;
                };
            });
        } catch (err) {
            console.log("failed insertFile", err);
            callbackFn(err);
        };
        
    }

    
    //ORIGIN: google init and file submission
    sendFiles = (callbackFn) => {
        var me = this,
        callbackCall = (res)=>{
            if(callbackFn){
                callbackFn(res);
            }
            else return res;
        };

        if (!navigator.onLine) {        
            callbackCall('Forms upload failed.  No internet connection.');
        } else {
            if (!gapi) {
                callbackCall('Forms upload failed.  Google Drive API not loaded.<br />Please re-load the app ensuring you have internet connection when you do so.');
            } else {
                // // Disable Submit button while transmission taking place
                
                // Show the spinner

                // Get library components
                try {

                    //  Establish Google Drive connection
                    googleConnection.makeGoogleConnection(function (result, google) {
                        // Callback function from Google Connection
                        try {
                            if (result/*.error*/ !== undefined) {
                                callbackCall("Google Connection failed");
                            };
                            // Get App Version
                            // googleApi.getAppVersion(function (result) {
                            //     if (!result.error && result !== "") {
                            //         // Check version number - break down to array of three elements [0]-Major, [1]-Minor and [2]-Build
                            //         var latestVersion = result.split(".");
                            //         var currentVersion = latestVersion.split(".");
                            //         if (parseInt(currentVersion[0], 10) < parseInt(latestVersion[0], 10) ||
                            //             (parseInt(currentVersion[0], 10) === parseInt(latestVersion[0], 10) && parseInt(currentVersion[1], 10) < parseInt(latestVersion[1], 10)) ||
                            //             (parseInt(currentVersion[0], 10) === parseInt(latestVersion[0], 10) && parseInt(currentVersion[1], 10) === parseInt(latestVersion[1], 10) && parseInt(currentVersion[2], 10) < parseInt(latestVersion[2], 10))) {
                            //             // Display warning message with link to load new version
                            //             console.log("Engineer Forms App", "There is a new version of this App, v" + result + ", now available for download.<br /><br />" +
                            //                 "Please ensure you do this at the earliest opportunity by opening Safari and use this url, <span class='text-white text-underline'>\"ipad.red-data.co.uk\"</span> to download the new version.<br /><br />" +
                            //                 "If you are unsure how to do this, please contact support for assistance.<br />" +
                            //                 "<a class='emailLink' id='supportLink' title='Tap here to send an email to support' href='#' onclick='contactSupport(); return false'>Tap here to send an email to support</a><br />");
                            //         } else {
                            //             sendAllFiles();
                            //         };
                            //     } else {
                            //         sendAllFiles();
                            //     };
                            // });

                            me.sendAllFiles(function (result) {
                                // Callback function from file submission
                                
                                try {
                                    if (result.error !== undefined) callbackCall(result);//throw result.error.message
                                    console.log('Forms Submission', result);
                                    if(result === true){
                                        dbActions.resetDoc(dbActions.toUpload);
                                        dbActions.resetDoc(dbActions.finalSub);
                                        if (callbackFn) callbackFn(result);
                                    }
                                } catch (err) {
                                    callbackCall('Forms Submission', 'Data transfer failed.<br><br>' + err);
                                } finally {
                                    // Hide spinner & re-enable Submit button
                                
                                };
                            });

                        } catch (err) {
                            console.log(err);
                            callbackCall('Form Submission', err);
                            // Hide spinner & re-enable Submit button
                        }
                    })
                } catch (err) {
                    callbackCall('Unable to submit forms.  Exception error: ' + err);
                };
            };
        };
    }

    //ENTRY POINT
    manualRun = (callbackFn) => {
        var that = this,
            callbackCall = (res)=>{
                if(callbackFn){
                    callbackFn(res);
                }
                else return res;
            };

        Promise.resolve(dbActions.getDoc(dbActions.finalSub))
            .then(function(doc){   
                if(doc){
                    Promise.resolve(that.createUploadDoc())
                    .then(function(){
                        Promise.resolve(that.fetchUploadDoc())
                        .then(function(){
                            that.sendFiles(function(result){
                                callbackCall(result);
                            });
                        });
                        
                    }).catch(function(err){
                        console.log(err);
                        callbackCall(err);
                    });
                }else{
                    callbackCall(null);
                }
            });
    };

    render(){
        return (
            <Wrapper manualRun={this.manualRun}>
                {this.props.children}
            </Wrapper>
        );
    }
}

export default GDrive;