import React, {cloneElement} from 'react';
import 'react-dom';
import createClass from 'create-react-class';

const Wrapper = createClass({
    render: function () {
        const {
            manualRun
        } = this.props;

        return (
            <div>
                {React.Children.map(this.props.children, child => cloneElement(child, {
                    manualRun
                }))}
            </div>
        );
    }
});

export default Wrapper;